<template>
  <div class="container">
    <div class="row mt-5">
      <div class="col-sm-12">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">Coronavirus</h5>
            <div class="progress" style="height: 20px;">
              <div
                class="progress-bar bg-danger"
                role="progressbar"
                v-bind:style="{ width: progressWidth + '%' }"
                :aria-valuenow="progressWidth"
                aria-valuemin="0"
                aria-valuemax="100"
              >
                {{ progressWidth }}%
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row mt-5">
      <div class="col-md-4">
        <div class="card">
          <div class="card-body">
            <h3>New Cases: <span class="badge badge-warning">{{ totalCases }}</span></h3>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="card">
          <div class="card-body">
            <h3>Deaths: <span class="badge badge-danger">{{ totalDeaths }}</span></h3>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="card">
          <div class="card-body">
            <h3>Recovered: <span class="badge badge-success">{{ totalRecovers }}</span></h3>
          </div>
        </div>
      </div>
    </div>

    <div class="row mt-5">
      <div class="col-sm-12" v-if="isRunning">
        <div class="card">
          <div class="card-header">Stop Coronavirus</div>
          <div class="card-body text-center">
            <button class="btn btn-success btn-lg" v-show="isLockdown === false" @click="lockdown">Lockdown</button>
            <div class="btn-group" v-show="isLockdown">
              <button type="button" class="btn btn-info btn-lg dropdown-toggle" :disabled="unLockDownBtnDisabled" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                Un-Lockdown
              </button>
              <div class="dropdown-menu">
                <a class="dropdown-item" href="javascript:void(0)" @click="unLockDownWithCoronaTest">
                  With Corona Test
                </a>
                <a class="dropdown-item" href="javascript:void(0)" @click="unLockDownWithoutCoronaTest">
                  Without Corona Test
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-md-6 offset-md-3 mb-5" v-else>
        <div class="card" v-if="isSaved">
          <div class="card-body">
            <h3 class="card-title text-center text-success">You saved the world</h3>
          </div>
        </div>
        <div class="card" v-else>
          <div class="card-body">
            <h3 class="card-title text-center text-danger">Game Over</h3>
            <!--<img class="card-img-top" :src="require('../assets/brown-chairs-inside-a-room-1440372.jpg')" />-->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      progressWidth: 0,
      totalCases: 0,
      totalDeaths: 0,
      totalRecovers: 0,
      totalPopulation: 100, //1326093247
      deathInterval: 0,
      time: 0,
      isRunning: true,
      interval: null,
      isLockdown: false,
      isSaved: false,
      unLockDownBtnDisabled: false
    };
  },

  created() {
    this.toggleTimer();
  },

  methods: {
    coronaStatus() {
      if (this.progressWidth != 100) {
        this.deathInterval = parseInt(this.deathInterval) + 1;

        if (this.deathInterval == 12) {
          this.totalDeaths = parseInt(this.totalDeaths) + 1;
          this.deathInterval = 0;
        } else {
          this.totalCases = (parseInt(this.totalCases) + 1);
        }

        this.progressWidth = Math.round(((this.totalCases + this.totalDeaths) / this.totalPopulation) * 100);
      } else {
        clearInterval(this.interval);
        this.gameEnd();
      }
    },
    toggleTimer() {
      if (this.isRunning) {
        this.interval = setInterval(this.incrementTime, 1000);
      } else {
        clearInterval(this.interval);
      }
      //this.isRunning = !this.isRunning;
    },
    incrementTime() {
      this.time = parseInt(this.time) + 1;
      this.coronaStatus();
    },
    lockdown() {
      this.isLockdown = true;
      if (this.isLockdown) {
        clearInterval(this.interval);
      } else {
        this.interval = setInterval(this.incrementTime, 1000);
      }
    },
    unLockDownWithCoronaTest() {
      if (this.unLockDownBtnDisabled === false) {
        this.unLockDownBtnDisabled = true;
      }

      if (this.totalCases > 0) {
        setTimeout(() => {
          this.totalCases = (parseInt(this.totalCases) - 1);
          this.totalRecovers = (parseInt(this.totalRecovers) + 1);
          this.unLockDownWithCoronaTest();
          this.progressWidth = Math.round((this.totalCases / (this.totalPopulation - this.totalDeaths)) * 100);
        }, 500);
      } else {
        this.isRunning = false;
        this.isSaved = true;
      }
    },
    unLockDownWithoutCoronaTest() {
      this.isLockdown = false;
      this.interval = setInterval(this.incrementTime, 1000);
    },
    gameEnd() {
      if (this.isLockdown === false) {
        if (this.totalCases > 0) {
          setTimeout(() => {
            this.totalCases = (parseInt(this.totalCases) - 1);
            this.totalDeaths = (parseInt(this.totalDeaths) + 1);
            this.gameEnd();
          }, 800);
        } else {
          this.isRunning = false;
          this.isSaved = false;
        }
      }
    },
    incrementDeaths() {
      if (this.totalCases > 0) {
        this.totalCases = (parseInt(this.totalCases) - 1);
        this.totalDeaths = (parseInt(this.totalDeaths) + 1);
      }
    }
  }
};
</script>
